// API client utilities for dashboard communication with FastAPI backend

import type { ApiResponse, PaginatedResponse } from '$lib/types/dashboard';

export class ApiError extends Error {
  constructor(
    message: string,
    public status: number,
    public response?: any
  ) {
    super(message);
    this.name = 'ApiError';
  }
}

export class ApiClient {
  private baseUrl: string;
  private token: string | null = null;

  constructor(baseUrl?: string) {
    this.baseUrl = baseUrl || (typeof window !== 'undefined' 
      ? window.location.origin 
      : 'http://localhost:8080');
  }

  setToken(token: string) {
    this.token = token;
  }

  clearToken() {
    this.token = null;
  }

  private async request<T>(
    endpoint: string,
    options: RequestInit = {}
  ): Promise<ApiResponse<T>> {
    const url = `${this.baseUrl}/api/v1${endpoint}`;
    
    const headers: Record<string, string> = {
      'Content-Type': 'application/json',
      ...(options.headers as Record<string, string> || {}),
    };

    if (this.token) {
      headers['Authorization'] = `Bearer ${this.token}`;
    }

    try {
      const response = await fetch(url, {
        ...options,
        headers,
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new ApiError(
          errorData.message || `HTTP ${response.status}: ${response.statusText}`,
          response.status,
          errorData
        );
      }

      const data = await response.json();
      return data;
    } catch (error) {
      if (error instanceof ApiError) {
        throw error;
      }
      throw new ApiError(`Network error: ${error instanceof Error ? error.message : 'Unknown error'}`, 0);
    }
  }

  // Authentication methods
  async login(email: string, password: string) {
    return this.request<{ token: string; refresh_token: string; user: any }>('/auth/login', {
      method: 'POST',
      body: JSON.stringify({ email, password }),
    });
  }

  async refreshToken(refreshToken: string) {
    return this.request<{ token: string; refresh_token: string }>('/auth/refresh', {
      method: 'POST',
      body: JSON.stringify({ refresh_token: refreshToken }),
    });
  }

  async logout() {
    return this.request<null>('/auth/logout', {
      method: 'POST',
    });
  }

  // Client methods
  async getClients(page: number = 1, pageSize: number = 20) {
    return this.request<PaginatedResponse<any>>(`/clients?page=${page}&page_size=${pageSize}`);
  }

  async getClient(clientId: string) {
    return this.request<any>(`/clients/${clientId}`);
  }

  async validateClient(clientId: string) {
    return this.request<any>(`/clients/${clientId}/validate`, {
      method: 'POST',
    });
  }

  // Metrics and analytics methods
  async getClientMetrics(clientId: string, timeframe: string = '24h') {
    return this.request<any>(`/clients/${clientId}/metrics?timeframe=${timeframe}`);
  }

  async getClientActivity(clientId: string, limit: number = 50, offset: number = 0) {
    return this.request<any>(`/clients/${clientId}/activity?limit=${limit}&offset=${offset}`);
  }

  async getSystemStatus() {
    return this.request<any>('/status');
  }

  // Integration methods
  async getIntegrations(clientId: string) {
    return this.request<any>(`/clients/${clientId}/integrations`);
  }

  async testIntegration(clientId: string, integrationId: string) {
    return this.request<any>(`/clients/${clientId}/integrations/${integrationId}/test`, {
      method: 'POST',
    });
  }

  // Automation methods
  async getAutomations(clientId: string) {
    return this.request<any>(`/clients/${clientId}/automations`);
  }

  async getAutomationStatus(clientId: string, automationId: string) {
    return this.request<any>(`/clients/${clientId}/automations/${automationId}/status`);
  }

  async pauseAutomation(clientId: string, automationId: string) {
    return this.request<any>(`/clients/${clientId}/automations/${automationId}/pause`, {
      method: 'POST',
    });
  }

  async resumeAutomation(clientId: string, automationId: string) {
    return this.request<any>(`/clients/${clientId}/automations/${automationId}/resume`, {
      method: 'POST',
    });
  }

  // Alert methods
  async getAlerts(clientId: string, resolved: boolean = false) {
    return this.request<any>(`/clients/${clientId}/alerts?resolved=${resolved}`);
  }

  async resolveAlert(clientId: string, alertId: string) {
    return this.request<any>(`/clients/${clientId}/alerts/${alertId}/resolve`, {
      method: 'POST',
    });
  }

  // Domain resolution
  async resolveDomain(domain: string) {
    return this.request<any>(`/domain/resolve?domain=${encodeURIComponent(domain)}`, {
      method: 'POST',
    });
  }
}

// Global API client instance
export const apiClient = new ApiClient();

// Utility functions for common operations
export async function withRetry<T>(
  operation: () => Promise<T>,
  maxRetries: number = 3,
  delay: number = 1000
): Promise<T> {
  let lastError: Error;

  for (let attempt = 1; attempt <= maxRetries; attempt++) {
    try {
      return await operation();
    } catch (error) {
      lastError = error instanceof Error ? error : new Error('Unknown error');
      
      if (attempt === maxRetries) {
        throw lastError;
      }

      // Exponential backoff
      await new Promise(resolve => setTimeout(resolve, delay * Math.pow(2, attempt - 1)));
    }
  }

  throw lastError!;
}

export function formatApiError(error: unknown): string {
  if (error instanceof ApiError) {
    return error.message;
  }
  
  if (error instanceof Error) {
    return error.message;
  }
  
  return 'An unexpected error occurred';
}