// Dashboard state management with real-time updates

import { writable, derived, get } from 'svelte/store';
import type { 
  DashboardState, 
  SystemMetrics, 
  ProcessingActivity, 
  Alert, 
  SystemAlert,
  ClientInfo,
  WebSocketMessage 
} from '$lib/types/dashboard';
import { apiClient, withRetry, formatApiError } from '$lib/utils/api';
import { DashboardWebSocket } from '$lib/utils/websocket';

// Initial state
const initialState: DashboardState = {
  client: null,
  metrics: null,
  automations: [],
  activities: [],
  integrations: [],
  alerts: [],
  analytics: null,
  websocket_connected: false,
  last_updated: '',
  loading: false,
  error: null
};

// Core writable store
const dashboardState = writable<DashboardState>(initialState);

// WebSocket instance
let websocketClient: DashboardWebSocket | null = null;

// Derived stores for specific data
export const client = derived(dashboardState, $state => $state.client);
export const metrics = derived(dashboardState, $state => $state.metrics);
export const automations = derived(dashboardState, $state => $state.automations);
export const activities = derived(dashboardState, $state => $state.activities);
export const integrations = derived(dashboardState, $state => $state.integrations);
export const alerts = derived(dashboardState, $state => $state.alerts);
export const analytics = derived(dashboardState, $state => $state.analytics);
export const isConnected = derived(dashboardState, $state => $state.websocket_connected);
export const isLoading = derived(dashboardState, $state => $state.loading);
export const error = derived(dashboardState, $state => $state.error);

// Computed metrics
export const unreadAlerts = derived(alerts, $alerts => 
  $alerts.filter(alert => !alert.resolved).length
);

export const criticalAlerts = derived(alerts, $alerts =>
  $alerts.filter(alert => alert.severity === 'critical' && !alert.resolved).length
);

export const recentActivities = derived(activities, $activities =>
  $activities.slice(0, 10)
);

class DashboardStore {
  private updateState(updates: Partial<DashboardState>) {
    dashboardState.update(state => ({
      ...state,
      ...updates,
      last_updated: new Date().toISOString()
    }));
  }

  private setLoading(loading: boolean) {
    this.updateState({ loading });
  }

  private setError(error: string | null) {
    this.updateState({ error });
  }

  // Initialize dashboard for a specific client
  async initialize(clientId: string, token?: string) {
    this.setLoading(true);
    this.setError(null);

    try {
      // Set authentication token if provided
      if (token) {
        apiClient.setToken(token);
      }

      // Load initial data in parallel
      const [
        clientResponse, 
        metricsResponse, 
        activitiesResponse, 
        alertsResponse,
        automationsResponse,
        integrationsResponse
      ] = await Promise.allSettled([
        withRetry(() => apiClient.getClient(clientId)),
        withRetry(() => apiClient.getClientMetrics(clientId)),
        withRetry(() => apiClient.getClientActivity(clientId)),
        withRetry(() => apiClient.getAlerts(clientId)),
        withRetry(() => apiClient.getAutomations(clientId)),
        withRetry(() => apiClient.getIntegrations(clientId))
      ]);

      // Process responses
      const client = clientResponse.status === 'fulfilled' ? clientResponse.value.data : null;
      const metrics = metricsResponse.status === 'fulfilled' ? metricsResponse.value.data : null;
      const activities = activitiesResponse.status === 'fulfilled' ? activitiesResponse.value.data : [];
      const alerts = alertsResponse.status === 'fulfilled' ? alertsResponse.value.data : [];
      const automations = automationsResponse.status === 'fulfilled' ? automationsResponse.value.data : [];
      const integrations = integrationsResponse.status === 'fulfilled' ? integrationsResponse.value.data : [];

      this.updateState({
        client,
        metrics,
        activities,
        alerts,
        automations,
        integrations,
        loading: false
      });

      // Initialize WebSocket connection
      if (token) {
        await this.connectWebSocket(clientId, token);
      }

      console.log('‚úÖ Dashboard initialized successfully for client:', clientId);

    } catch (error) {
      console.error('‚ùå Failed to initialize dashboard:', error);
      this.setError(formatApiError(error));
      this.setLoading(false);
    }
  }

  // WebSocket connection management
  async connectWebSocket(clientId: string, token: string) {
    try {
      // Disconnect existing connection
      if (websocketClient) {
        websocketClient.disconnect();
      }

      // Create new WebSocket connection
      websocketClient = new DashboardWebSocket(clientId, token);

      // Set up event handlers
      websocketClient.on('connected', () => {
        console.log('üîó Dashboard WebSocket connected');
        this.updateState({ websocket_connected: true });
      });

      websocketClient.on('disconnected', () => {
        console.log('üîå Dashboard WebSocket disconnected');
        this.updateState({ websocket_connected: false });
      });

      websocketClient.on('error', () => {
        console.error('‚ùå Dashboard WebSocket error');
        this.updateState({ websocket_connected: false });
      });

      // Handle real-time updates
      websocketClient.on('metrics_update', (message: WebSocketMessage) => {
        if (message.data && message.type === 'metric_update') {
          this.updateState({ metrics: message.data as SystemMetrics });
        }
      });

      websocketClient.on('activity_update', (message: WebSocketMessage) => {
        if (message.data && message.type === 'activity_feed') {
          const currentState = get(dashboardState);
          const newActivities = [message.data as ProcessingActivity, ...currentState.activities].slice(0, 100);
          this.updateState({ activities: newActivities });
        }
      });

      websocketClient.on('alert_update', (message: WebSocketMessage) => {
        if (message.data && message.type === 'system_alert') {
          const alertData = message.data as SystemAlert;
          const currentState = get(dashboardState);
          const existingIndex = currentState.alerts.findIndex(a => a.id === alertData.id);
          
          let newAlerts;
          if (existingIndex >= 0) {
            // Update existing alert
            newAlerts = [...currentState.alerts];
            newAlerts[existingIndex] = alertData;
          } else {
            // Add new alert
            newAlerts = [alertData, ...currentState.alerts];
          }
          
          this.updateState({ alerts: newAlerts });
        }
      });

      websocketClient.on('client_update', (message: WebSocketMessage) => {
        if (message.data) {
          // Only update if we can safely cast to ClientInfo
          try {
            const clientData = message.data as unknown as ClientInfo;
            if (clientData.id && clientData.name) {
              this.updateState({ client: clientData });
            }
          } catch (error) {
            console.warn('Failed to update client data:', error);
          }
        }
      });

      // Connect
      await websocketClient.connect();

    } catch (error) {
      console.error('‚ùå Failed to connect WebSocket:', error);
      this.updateState({ websocket_connected: false });
    }
  }

  // Manual refresh
  async refresh(clientId: string) {
    const currentState = get(dashboardState);
    if (currentState.loading) return; // Prevent multiple concurrent refreshes

    this.setLoading(true);

    try {
      // Refresh all data
      const [metricsResponse, activitiesResponse, alertsResponse] = await Promise.allSettled([
        apiClient.getClientMetrics(clientId),
        apiClient.getClientActivity(clientId),
        apiClient.getAlerts(clientId)
      ]);

      const updates: Partial<DashboardState> = {};

      if (metricsResponse.status === 'fulfilled') {
        updates.metrics = metricsResponse.value.data;
      }

      if (activitiesResponse.status === 'fulfilled') {
        updates.activities = activitiesResponse.value.data;
      }

      if (alertsResponse.status === 'fulfilled') {
        updates.alerts = alertsResponse.value.data;
      }

      this.updateState({ ...updates, loading: false });

    } catch (error) {
      console.error('‚ùå Failed to refresh dashboard:', error);
      this.setError(formatApiError(error));
      this.setLoading(false);
    }
  }

  // Resolve alert
  async resolveAlert(clientId: string, alertId: string) {
    try {
      await apiClient.resolveAlert(clientId, alertId);
      
      // Update local state
      const currentState = get(dashboardState);
      const updatedAlerts = currentState.alerts.map(alert =>
        alert.id === alertId ? { ...alert, resolved: true } : alert
      );
      
      this.updateState({ alerts: updatedAlerts });

    } catch (error) {
      console.error('‚ùå Failed to resolve alert:', error);
      this.setError(formatApiError(error));
    }
  }

  // Cleanup
  destroy() {
    if (websocketClient) {
      websocketClient.disconnect();
      websocketClient = null;
    }
    
    // Clear authentication
    apiClient.clearToken();
    
    // Reset state
    dashboardState.set(initialState);
  }

  // Utility methods
  async loadMoreActivities(clientId: string, offset: number = 0) {
    try {
      const response = await apiClient.getClientActivity(clientId, 50, offset);
      const currentState = get(dashboardState);
      const newActivities = [...currentState.activities, ...response.data];
      
      this.updateState({ activities: newActivities });
      
    } catch (error) {
      console.error('‚ùå Failed to load more activities:', error);
      this.setError(formatApiError(error));
    }
  }

  async updateTimeframe(clientId: string, timeframe: string) {
    this.setLoading(true);
    
    try {
      const response = await apiClient.getClientMetrics(clientId, timeframe);
      this.updateState({ 
        metrics: response.data,
        loading: false 
      });
      
    } catch (error) {
      console.error('‚ùå Failed to update timeframe:', error);
      this.setError(formatApiError(error));
      this.setLoading(false);
    }
  }
}

// Export singleton instance
export const dashboardStore = new DashboardStore();

// Export the main store for direct access
export { dashboardState };