// WebSocket client for real-time dashboard updates

import type { WebSocketMessage } from '$lib/types/dashboard';

export type WebSocketEventHandler = (message: WebSocketMessage) => void;

export class DashboardWebSocket {
  private ws: WebSocket | null = null;
  private url: string;
  private clientId: string;
  private token: string;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 10;
  private reconnectDelay = 1000;
  private handlers = new Map<string, Set<WebSocketEventHandler>>();
  private isConnecting = false;
  private shouldReconnect = true;

  constructor(clientId: string, token: string, baseUrl?: string) {
    this.clientId = clientId;
    this.token = token;
    
    // Determine WebSocket URL
    if (typeof window !== 'undefined') {
      const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
      const host = baseUrl ? new URL(baseUrl).host : window.location.host;
      this.url = `${protocol}//${host}/ws/client/${clientId}`;
    } else {
      // Server-side rendering fallback
      this.url = `ws://localhost:8080/ws/client/${clientId}`;
    }
  }

  connect(): Promise<void> {
    return new Promise((resolve, reject) => {
      if (this.ws && this.ws.readyState === WebSocket.OPEN) {
        resolve();
        return;
      }

      if (this.isConnecting) {
        reject(new Error('Connection already in progress'));
        return;
      }

      this.isConnecting = true;

      try {
        // Add authentication token to WebSocket URL
        const wsUrl = new URL(this.url);
        wsUrl.searchParams.set('token', this.token);
        
        this.ws = new WebSocket(wsUrl.toString());

        this.ws.onopen = () => {
          console.log(`üîó WebSocket connected to ${this.url}`);
          this.isConnecting = false;
          this.reconnectAttempts = 0;
          this.emit('connected', null);
          resolve();
        };

        this.ws.onmessage = (event) => {
          try {
            const message: WebSocketMessage = JSON.parse(event.data);
            this.handleMessage(message);
          } catch (error) {
            console.error('Failed to parse WebSocket message:', error);
          }
        };

        this.ws.onclose = (event) => {
          console.log(`üîå WebSocket disconnected: ${event.code} ${event.reason}`);
          this.isConnecting = false;
          this.ws = null;
          this.emit('disconnected', null);

          if (this.shouldReconnect && !event.wasClean) {
            this.scheduleReconnect();
          }
        };

        this.ws.onerror = (error) => {
          console.error('WebSocket error:', error);
          this.isConnecting = false;
          this.emit('error', null);
          
          if (this.ws?.readyState !== WebSocket.OPEN) {
            reject(new Error('WebSocket connection failed'));
          }
        };

        // Connection timeout
        setTimeout(() => {
          if (this.isConnecting) {
            this.isConnecting = false;
            this.ws?.close();
            reject(new Error('WebSocket connection timeout'));
          }
        }, 10000);

      } catch (error) {
        this.isConnecting = false;
        reject(error);
      }
    });
  }

  disconnect() {
    this.shouldReconnect = false;
    if (this.ws) {
      this.ws.close(1000, 'Client disconnecting');
      this.ws = null;
    }
  }

  private scheduleReconnect() {
    if (this.reconnectAttempts >= this.maxReconnectAttempts) {
      console.error('Max reconnection attempts reached');
      this.emit('max_reconnect_attempts', null);
      return;
    }

    this.reconnectAttempts++;
    const delay = this.reconnectDelay * Math.pow(2, this.reconnectAttempts - 1);
    
    console.log(`Attempting to reconnect in ${delay}ms (attempt ${this.reconnectAttempts})`);
    
    setTimeout(() => {
      if (this.shouldReconnect) {
        this.connect().catch((error) => {
          console.error('Reconnection failed:', error);
        });
      }
    }, delay);
  }

  private handleMessage(message: WebSocketMessage) {
    // Emit to type-specific handlers
    this.emit(message.type, message);
    
    // Emit to general message handler
    this.emit('message', message);
  }

  private emit(eventType: string, message: WebSocketMessage | null) {
    const handlers = this.handlers.get(eventType);
    if (handlers) {
      handlers.forEach(handler => {
        try {
          handler(message as WebSocketMessage);
        } catch (error) {
          console.error(`Error in WebSocket handler for ${eventType}:`, error);
        }
      });
    }
  }

  // Event subscription methods
  on(eventType: string, handler: WebSocketEventHandler) {
    if (!this.handlers.has(eventType)) {
      this.handlers.set(eventType, new Set());
    }
    this.handlers.get(eventType)!.add(handler);
  }

  off(eventType: string, handler: WebSocketEventHandler) {
    const handlers = this.handlers.get(eventType);
    if (handlers) {
      handlers.delete(handler);
      if (handlers.size === 0) {
        this.handlers.delete(eventType);
      }
    }
  }

  // Send message to server
  send(data: any) {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify(data));
    } else {
      console.warn('WebSocket not connected, unable to send message');
    }
  }

  // Utility methods
  get isConnected(): boolean {
    return this.ws?.readyState === WebSocket.OPEN;
  }

  get connectionState(): string {
    if (!this.ws) return 'disconnected';
    
    switch (this.ws.readyState) {
      case WebSocket.CONNECTING: return 'connecting';
      case WebSocket.OPEN: return 'connected';
      case WebSocket.CLOSING: return 'closing';
      case WebSocket.CLOSED: return 'disconnected';
      default: return 'unknown';
    }
  }
}